.game-info-wrap {
    padding: 35px 40px;
}

.game-info-title {
    font-size: 45px;
    font-weight: bold;
    color: #fff;
    margin: 30px 0;
    letter-spacing: 3px;
}

.game-info-subtitle {
    margin-bottom: 13px;
    font-size: 18px;
}

.game-info-link {
    transition: 0.3s;
}

.game-info-link:hover {
    color: #757575;
    transition: 0.3s;
}

.game-info-description {
    font-size: 18px;
    margin-top: 5px;
}

.game-about-subtitle {
    margin: 30px 0 13px;
}

.game-slider-wrap {
    padding: 35px 40px;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: #151515;
}

.swiper {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: 100%;
}

.swiper-button-prev {
    color: rgb(95, 94, 94);
}

.swiper-button-next {
    color: rgb(95, 94, 94);
}

.swiper-pagination-bullet {
    background: #757575;
}


@media (max-width: 991.98px) {
    .game-info-wrap {
        padding: 10px 40px;
    }

    .game-info-title {
        font-size: 35px;
    }

    .game-info-description {
        font-size: 16px;
    }

    .game-info-link {
        font-size: 15px;
    }
}


@media (max-width: 767.98px) {
    .game-info-title {
        font-size: 28px;
        margin: 18px 0;
    }

    .game-info-link {
        font-size: 14px;
    }

    .game-about-subtitle {
        font-size: 18px;
    }
}


@media (max-width: 575.98px) {
    .game-info-wrap {
        padding: 10px 25px;
    }

    .game-info-title {
        font-size: 21px;
        margin: 20px 0 10px;
    }

    .game-about-subtitle {
        margin: 20px 0 0;
        font-size: 13px;
    }

    .game-info-subtitle {
        margin-bottom: 3px;
        font-size: 13px;
    }

    .game-info-description {
        font-size: 12px;
    }

    .game-slider-wrap {
        padding: 10px 25px;
    }

    .game-link {
        font-size: 12px;
    }
}