.sort-select {
    padding: 10px 16px;
    background-color: hsla(0, 0%, 100%, .07);
    border-radius: 8px;
    font-size: 14px;
    color: rgb(216, 213, 213);
    border: none;
    cursor: pointer;
    outline: none;
    transition: 0.3s;
}

.sort-select option {
    color: rgb(17, 17, 17);
    margin: 15px;
}

@media (max-width: 767.98px) {
    .sort-select {
        padding: 10px 14px;
        margin-bottom: 0 10px 10px 0;
    }
}

@media (max-width: 575.98px) {
    .sort-select {
        width: 100%;
    }
}